import BaseModule from './BaseModule';
export default class Navigation extends BaseModule {
  register() {
    this.SCROLLY_THREADSHOT = 30;
    this.windowWidth = window.innerWidth;
    this.CLS_EXPAND = 'expand';
    this.CLS_SHOWNAV = 'show-nav';
    this.navCollapse = this.el.querySelector('.navbar-collapse');
    this.navToggleer = this.el.querySelector('.navbar-toggler');
    this.menusHasSub = this.el.querySelectorAll('.menu-item-has-children');

    console.log(this.el);

    this.navToggleer.addEventListener('click', (e) => {
      console.log(e.currentTarget);
      e.currentTarget.classList.toggle('collapsed');
      console.log(this.navCollapse);
      if (this.navCollapse.classList.contains(this.CLS_EXPAND)) {
        this.navCollapse.classList.remove(this.CLS_EXPAND);
        document.body.classList.remove(this.CLS_SHOWNAV);
        this.navCollapse.style.display = 'none';
      } else {
        this.navCollapse.style.display = 'block';
        this.navCollapse.classList.add(this.CLS_EXPAND);
        document.body.classList.add(this.CLS_SHOWNAV);
      }
    });

    this.menusHasSub.forEach((element) => {
      //   element.ap(
      //     `<span class="plus">+</span> <span class="minus">-</span>`
      //   );
      const domPlus = document.createElement('span');
      domPlus.classList.add('plus');
      domPlus.innerText = '+';
      element.appendChild(domPlus);
      domPlus.addEventListener('click', (e) => {
        this.menusHasSub.forEach((item) => {
          item.classList.remove('open');
        });
        e.currentTarget.parentElement.classList.add('open');
      });

      const domMinus = document.createElement('span');
      domMinus.classList.add('minus');
      domMinus.innerText = '-';
      element.appendChild(domMinus);
      domMinus.addEventListener('click', (e) => {
        e.currentTarget.parentElement.classList.remove('open');
      });
    });

    window.addEventListener('scroll', () => {
      this.checkStickyNav();
    });

    window.addEventListener('resize', () => {
      if (window.innerWidth != this.windowWidth) {
        this.windowWidth = window.innerWidth;
        this.resize();
      }
    });
  }

  checkStickyNav() {
    if (window.scrollY > this.SCROLLY_THREADSHOT) {
      this.el.classList.add('sticky');
    } else {
      this.el.classList.remove('sticky');
    }
  }

  resize() {
    if (
      document.body.classList.contains(this.CLS_SHOWNAV) &&
      window.innerWidth >= 992
    ) {
      this.navCollapse.classList.remove(this.CLS_EXPAND);
      document.body.classList.remove(this.CLS_SHOWNAV);
    }
  }
}
