import BaseModule from './BaseModule';
import Swiper from 'swiper';
import 'swiper/swiper-bundle.css';

// core version + navigation, pagination modules:
import SwiperCore, {
  Navigation,
  Pagination,
  Autoplay,
  Lazy,
  EffectFade,
} from 'swiper/core';

// configure Swiper to use modules
SwiperCore.use([Navigation, Pagination, Autoplay, Lazy, EffectFade]);
export default class SwiperBegin extends BaseModule {
  register() {
    if (this.el.querySelector('.swiper-catList')) {
      const swiperCatList = new Swiper('.swiper-catList', {
        spaceBetween: 30,
        slidesPerView: 1,
        speed: 3000,
        transitionDuration: 1000,
        centerInsufficientSlides: true,
        autoplay: {
          delay: 3500,
          disableOnInteraction: false,
          stopOnLastSlide: true,
        },
        breakpoints: {
          // when window width is >= 1024
          768: {
            slidesPerView: 2,
          },
          // when window width is >= 1024
          1024: {
            slidesPerView: 5,
          },
        },
      });
    }
    if (this.el.querySelector('.swiper-banner')) {
      const swiperBanner = new Swiper('.swiper-banner', {
        spaceBetween: 30,
        centeredSlides: true,
        effect: 'fade',
        speed: 2000,
        fadeEffect: {
          crossFade: true,
        },
        autoplay: {
          delay: 3000,
          disableOnInteraction: false,
          stopOnLastSlide: true,
        },
        pagination: {
          el: '.swiper-banner .swiper-pagination',
          clickable: true,
        },
      });
    }
    if (this.el.querySelector('.swiper-beloved')) {
      const swiperBeloved = new Swiper('.swiper-beloved', {
        spaceBetween: 30,
        slidesPerView: 2,
        // centeredSlides: true,
        centerInsufficientSlides: true,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false,
          stopOnLastSlide: true,
        },
        breakpoints: {
          // when window width is >= 1024
          768: {
            slidesPerView: 3,
          },
          // when window width is >= 1024
          1024: {
            slidesPerView: 5,
          },
        },
        navigation: {
          nextEl: '.bl.swiper-button-next',
          prevEl: '.bl.swiper-button-prev',
        },
      });
    }
    if (this.el.querySelector('.swiper-relatedPost')) {
      const sliderRelatedPost = new Swiper('.swiper-relatedPost', {
        spaceBetween: 30,
        slidesPerView: 1,
        // centeredSlides: true,
        centerInsufficientSlides: true,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false,
          stopOnLastSlide: true,
        },
        breakpoints: {
          // when window width is >= 1024
          768: {
            slidesPerView: 2,
          },
          // when window width is >= 1024
          1024: {
            slidesPerView: 3,
          },
        },
        navigation: {
          nextEl: '.bl.swiper-button-next',
          prevEl: '.bl.swiper-button-prev',
        },
      });
    }
    if (this.el.querySelector('.wc-block-grid__products')) {
      const swiperBeloved = new Swiper('.wc-block-grid__products', {
        spaceBetween: 30,
        slidesPerView: 2,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false,
          stopOnLastSlide: true,
        },
        breakpoints: {
          // when window width is >= 1024
          768: {
            slidesPerView: 3,
          },
          // when window width is >= 1024
          1024: {
            slidesPerView: 5,
          },
        },
        navigation: {
          nextEl: '.bl.swiper-button-next',
          prevEl: '.bl.swiper-button-prev',
        },
      });
    }
    if (this.el.querySelector('.swiper-testimonial')) {
      const swiperTest = new Swiper('.swiper-testimonial', {
        spaceBetween: 50,
        slidesPerView: 1,
        autoplay: {
          delay: 3500,
          disableOnInteraction: false,
          stopOnLastSlide: true,
        },
        breakpoints: {
          // when window width is >= 1024
          768: {
            slidesPerView: 2,
          },
          // when window width is >= 1024
          1024: {
            slidesPerView: 3,
          },
        },
        navigation: {
          nextEl: '.ts.swiper-button-next',
          prevEl: '.ts.swiper-button-prev',
        },
        pagination: {
          el: '.swiper-testimonial .swiper-pagination',
          clickable: true,
        },
      });
    }
  }
}
