import BaseModule from './BaseModule';
export default class ProductDetail extends BaseModule {
  register() {
    this.showCount = this.el.querySelector('.quantity .input-text')
    this.btnDes = this.el.querySelector('.quantity .btn-minus')
    this.btnInc = this.el.querySelector('.quantity .btn-plus')
    if (this.showCount) {
      this.btnDes.addEventListener('click', () =>this.decreaseValue())
      this.btnInc.addEventListener('click', () =>this.increaseValue())      
    }
  }

  increaseValue() {
    let value = parseInt(this.showCount.value, 10);
    value = isNaN(value) ? 0 : value;
    value++;
    this.showCount.value = value;
  }

  decreaseValue() {
    let value = parseInt(this.showCount.value, 10);
    value = isNaN(value) ? 0 : value;
    value < 1 ? value = 1 : '';
    value--;
    this.showCount.value = value;
  }
}