import BaseModule from './BaseModule';
import { TweenMax } from 'gsap';

export default class Animation extends BaseModule {
  register() {
    this.animateItems = Array.from(this.el.querySelectorAll('.js--animate'));
    this.vw = window.innerWidth;
    window.addEventListener('load', () => {
      this.animate();
    });

    window.addEventListener('scroll', () => {
      this.animate();
    });

    window.addEventListener('resize', () => {
      this.resizeTimer && clearTimeout(this.resizeTimer);
      this.resizeTimer = setTimeout(() => {
        if (this.vw !== window.innerWidth) {
          this.vw = window.innerWidth;
          this.animateItems.forEach((ai) => {
            ai.classList.remove('animated');
            ai.classList.add('js--animate');
          });
          this.animate();
        }
      }, 400);
    });
  }

  checkInViewport(el, offset = 0) {
    const bounding = el.getBoundingClientRect();
    return bounding.top < window.innerHeight - offset;
  }

  animate() {
    this.animateItems.forEach((item) => {
      const offset = parseInt(item.getAttribute('data-offset')) || 0;
      if (this.checkInViewport(item, offset)) {
        const animation = item.getAttribute('data-animation');
        if (!animation) return;
        if (!item.classList.contains('js--animate')) return;
        const delay = parseFloat(item.getAttribute('data-delay')) || 0;
        const duration = parseFloat(item.getAttribute('data-duration')) || 0;
        const from = parseFloat(item.getAttribute('data-from')) || 0;
        const depend = [];
        const selectors = (item.getAttribute('data-depend') || '')
          .split(',')
          .filter((sl) => sl.length);
        selectors.forEach((sl) => {
          const els = this.el.querySelectorAll(sl);
          els.forEach((el) => {
            depend.push(el);
          });
        });
        this[animation](item, { delay, from, duration, depend });
        item.classList.remove('js--animate');
      }
    });
  }

  fromLeft(el, options) {
    if (options.depend) {
      if (!this.checkDepend(options.depend)) {
        requestAnimationFrame(() => {
          this.fromLeft(el, options);
        });
        return;
      }
    }
    TweenMax.fromTo(
      el,
      options.duration || 0.4,
      {
        opacity: 0,
        x: options.from || -500,
      },
      {
        opacity: 1,
        x: 0,
        delay: options.delay || 0,
        onComplete: () => {
          el.classList.add('animated');
        },
      }
    );
  }

  fromRight(el, options) {
    if (options.depend) {
      if (!this.checkDepend(options.depend)) {
        requestAnimationFrame(() => {
          this.fromLeft(el, options);
        });
        return;
      }
    }
    TweenMax.fromTo(
      el,
      options.duration || 0.4,
      {
        opacity: 0,
        x: options.from || 500,
      },
      {
        opacity: 1,
        x: 0,
        delay: options.delay || 0,
        onComplete: () => {
          el.classList.add('animated');
        },
      }
    );
  }

  fromBottom(el, options) {
    if (options.depend) {
      if (!this.checkDepend(options.depend)) {
        requestAnimationFrame(() => {
          this.fromBottom(el, options);
        });
        return;
      }
    }
    TweenMax.fromTo(
      el,
      options.duration || 0.8,
      {
        opacity: 0,
        y: options.from || 200,
      },
      {
        opacity: 1,
        y: 0,
        delay: options.delay || 0,
        onComplete: () => {
          el.classList.add('animated');
        },
      }
    );
  }

  fillRight(el, options) {
    if (options.depend) {
      if (!this.checkDepend(options.depend)) {
        requestAnimationFrame(() => {
          this.fillRight(el, options);
        });
        return;
      }
    }
    TweenMax.from(el, options.duration || 0.4, {
      width: 0,
      delay: options.delay || 0,
      onComplete: () => {
        el.classList.add('animated');
      },
    });
  }

  scaleUp(el, options) {
    if (options.depend) {
      if (!this.checkDepend(options.depend)) {
        requestAnimationFrame(() => {
          this.scaleUp(el, options);
        });
        return;
      }
    }
    TweenMax.fromTo(
      el,
      options.duration || 0.4,
      {
        opacity: 0,
        scale: 0,
      },
      {
        opacity: 1,
        scale: 1,
        delay: options.delay || 0,
        onComplete: () => {
          el.classList.add('animated');
        },
      }
    );
  }

  opacityOnly(el, options) {
    if (options.depend) {
      if (!this.checkDepend(options.depend)) {
        requestAnimationFrame(() => {
          this.opacityOnly(el, options);
        });
        return;
      }
    }
    TweenMax.fromTo(
      el,
      options.duration || 0.4,
      {
        opacity: 0,
      },
      {
        opacity: 1,
        delay: options.delay || 0,
        onComplete: () => {
          el.classList.add('animated');
        },
      }
    );
  }

  scaleDown(el, options) {
    if (options.depend) {
      if (!this.checkDepend(options.depend)) {
        requestAnimationFrame(() => {
          this.scaleDown(el, options);
        });
        return;
      }
    }
    TweenMax.fromTo(
      el,
      options.duration || 0.4,
      {
        opacity: 0,
        scale: options.from || 2,
      },
      {
        opacity: 1,
        scale: 1,
        delay: options.delay || 0,
        onComplete: () => {
          el.classList.add('animated');
        },
      }
    );
  }

  // scaleDown(el, options) {
  //   TweenLite.fromTo(el, 0.4, {
  //     opacity: 0,
  //     scale: 3
  //   }, {
  //     opacity: 1,
  //     scale: 1,
  //     delay: options.delay || 0
  //   })
  // }
  // scaleUp(el, options) {
  //   TweenLite.fromTo(el, 0.4, {
  //     opacity: 0,
  //     scale: 0
  //   }, {
  //     opacity: 1,
  //     scale: 1,
  //     delay: options.delay || 0
  //   })
  // }
  // moveUp(el, options) {
  //   TweenLite.fromTo(el, 1, {
  //     opacity: 0,
  //     y: options.from || 500
  //   }, {
  //     opacity: 1,
  //     y: 0,
  //     delay: options.delay || 0
  //   })
  // }
  // expand(el, options) {
  //   TweenLite.fromTo(el, 1, {
  //     width: 0
  //   }, {
  //     width: el.scrollWidth
  //   })
  // }

  computedDepend(el) {
    const depend = [];
    const selectors = (el.getAttribute('data-depend') || '')
      .split(',')
      .filter((sl) => sl.length);
    selectors.forEach((sl) => {
      const els = this.el.querySelectorAll(sl.trim());
      els.forEach((el) => {
        depend.push(el);
      });
    });

    return depend;
  }

  checkDepend(depend) {
    for (let i = 0; i < depend.length; i++) {
      if (!depend[i].classList.contains('animated')) {
        return false;
      }
    }

    return true;
  }
}
