import BaseModule from './BaseModule';
export default class Accordion extends BaseModule {
  register() {
    this.CLS_EXPAND = 'expand';
    this.CLS_SHOWNAV = 'show';
    this.AccordionItem = this.el.querySelectorAll('.accordion-item');

    this.AccordionItem.forEach((element) => {
      console.log(element);

      element.querySelector('.acc-link').addEventListener('click', (e) => {
        e.preventDefault();
        e.currentTarget.classList.contains('show')
          ? e.currentTarget.classList.remove('show')
          : e.currentTarget.classList.add('show');
      });
    });
  }
}
